body {
  position: absolute;
  margin: 0 0 0 0;
  overflow-x : hidden;
  overflow-y : hidden;}

.dialog-close {
  position: absolute;
  right: 5px;
  top: 5px;
  width: 12px;
  height: 12px;
  cursor: pointer;
  background-image: url("../img/icon_close9x9_normal.png");
  background-repeat: no-repeat;
  z-index: 11;
}